function sliderCatalogTop() {
    var swiper = new Swiper('.catalog-top .swiper', {
        slidesPerView: '5',
        spaceBetween: 0,
        navigation: {
            nextEl: '.catalog-top .swiper-button-next',
            prevEl: '.catalog-top .swiper-button-prev',
        },
        breakpoints: {
            320: {
                slidesPerView: '1',
            },
            577: {
                slidesPerView: '2',
            },
            769: {
                slidesPerView: '3',
            },
            993: {
                slidesPerView: '5',
            },
        }
    })
}

function sliderCatalogContent() {
    var swiper = new Swiper('.catalog__content .swiper', {
        slidesPerView: '4',
        spaceBetween: 20,
        navigation: {
            nextEl: '.catalog__content .swiper-button-next',
            prevEl: '.catalog__content .swiper-button-prev',
        },
        breakpoints: {
            320: {
                slidesPerView: '1',
            },
            651: {
                slidesPerView: '2',
            },
            901: {
                slidesPerView: '3',
            },
            993: {
                slidesPerView: '4',
            },
        }
    })
}

function sliderAnswers() {
    var swiper = new Swiper('.answers .swiper', {
        slidesPerView: '4',
        spaceBetween: 20,
        navigation: {
            nextEl: '.answers .swiper-button-next',
            prevEl: '.answers .swiper-button-prev',
        },
        breakpoints: {
            320: {
                slidesPerView: '1',
            },
            577: {
                slidesPerView: '2',
            },
            901: {
                slidesPerView: '3',
            },
            993: {
                slidesPerView: '4',
            },
        }
    })
}

$(document).ready(function() {
    sliderCatalogTop();
    sliderCatalogContent();
    sliderAnswers();


    //Hover-item
    $(".header-top__item2").hover(onIn, onOut);
    $(".header-top__item2 .header-top__item2-icon").click(function() {
        if ($(this).parents(".header-top__item2").hasClass("header-top__item2--active")) {
            $(this).parents(".header-top__item2").removeClass("header-top__item2--active")
        } else {
            $(this).parents(".header-top__item2").addClass("header-top__item2--active")
        }
    })

    $('.header-top__item2-close').click(function () {
        $(this).parents('.header-top__item2').removeClass('header-top__item2--active')
    })

    //Burger-menu
    $('.burger').click(function () {
        $(this).toggleClass('burger--active')
        $('.header-top__right').toggleClass('header-top__right--active')
        $('body').toggleClass('fixed-body')
    })

    //Tabs
    $(".tab").click(function () {
        let path = $(this).attr("data-tab-path")
        $(this).siblings(".tab").removeClass("tab--active")
        $(this).addClass("tab--active")

        $(this).parents(".tabs-inner").find(".tab__content").removeClass("tab__content--active")
        $(this).parents(".tabs-inner").find(`.tab__content[data-content-path="${path}"]`).addClass("tab__content--active")
    })

    //Favorite
    $('.favorite').click(function () {
        $(this).toggleClass('favorite--active')
    })

    // Calc
    function cartCalc(){
        $('.cartcalc .ccalc-minus').click(function(){
            let a = $(this).closest('.cartcalc').find('input').val();
            if(a>1){
                let b = +a-1;
                $(this).closest('.cartcalc').find('input').val(b);
            }else{
                $(this).closest('.cartcalc').find('input').val(a);
            }
        });
        $('.cartcalc .ccalc-plus').click(function(){
            let a = $(this).closest('.cartcalc').find('input').val();
            let b = +a+1;
            $(this).closest('.cartcalc').find('input').val(b);
        });
    }
    cartCalc()

    //Calc + search
    $('.fixed-item').hover(function () {
        $(this).toggleClass('fixed-item--active')
    })

    //Input
    let starInput = document.querySelectorAll('.star-input');
    let starLabel = document.querySelectorAll('.star-label');

    for(let i = 0; i < starInput.length; i++){
        starInput[i].addEventListener('input', function(){
            starLabel[i].style.display = ( this.value == "" ) ? 'inline' : 'none';
        });
    }

    //Accordion
    $(".product-info__content-show").click((function() {
        $(this).toggleClass("product-info__content-show--active")
        $(this).siblings(".product-info__content-hidden").slideToggle()
    }));


})

function onIn() {
    if (window.innerWidth > 992) {
        let el = $(this)
        setTimeout(function() {
            if (el.is(':hover')) {
                console.log(el)
                el.addClass("header-top__item2--active")

            }
        }, 200);
    }
}

function onOut() {
    if (window.innerWidth > 992) {
        $(this).removeClass("header-top__item2--active")
    }
}